* {
  box-sizing: border-box;
}

body {
  background: #23a6d5 0 0 / 400% 400%;
  font-family: -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Oxygen, Ubuntu, Cantarell, Open Sans, Helvetica Neue, sans-serif;
  animation: 15s infinite gradient;
}

a {
  text-decoration: none;
}

p {
  margin: 0;
}

.search-form {
  justify-content: center;
  width: 100%;
  padding-top: 30px;
  display: flex;
}

.search-form input {
  border: none;
  border-radius: 5px 0 0 5px;
  outline: none;
  width: 20%;
  height: 40px;
  padding: 8px 10px;
}

.search-form button {
  cursor: pointer;
  background-color: #fff;
  border: none;
  border-radius: 0 5px 5px 0;
  justify-content: center;
  align-items: center;
  padding: 8px 20px;
  display: flex;
}

.search-form button:hover {
  background-color: rgba(224, 224, 224, .5);
  transition: all .3s;
}

.load-more.is-hidden {
  display: none;
}

.container {
  padding-bottom: 20px;
  padding-left: 15px;
  padding-right: 15px;
}

.gallery, .info {
  flex-wrap: wrap;
  display: flex;
}

.gallery {
  justify-content: center;
  margin-left: -20px;
  padding-top: 30px;
}

.gallery__link {
  border: none;
  border-radius: 10px;
  flex-basis: calc(25% - 20px);
  min-width: 320px;
  margin-top: 20px;
  margin-left: 20px;
  transition: all .3s;
  overflow: hidden;
}

.gallery__link:hover, .gallery__link:focus {
  transform: scale(1.05);
}

.gallery-item {
  flex-direction: column;
  justify-content: space-evenly;
  display: flex;
}

.gallery-item__img {
  -o-object-fit: cover;
  object-fit: cover;
  height: 30vh;
  display: block;
}

.info {
  justify-content: space-around;
  padding: 10px;
}

.info-item {
  color: #fff;
  flex-direction: column;
  align-items: center;
  font-size: smaller;
  display: flex;
}

.info-item:not(:first-child) {
  margin-left: 5px;
}

#arrowTop {
  cursor: pointer;
  z-index: 2;
  font-size: 2.5em;
  position: fixed;
  bottom: 10px;
  right: 10px;
}

#arrowTop:before {
  content: "▲";
}

